



















import { Vue, Component, Prop } from 'vue-property-decorator'
import GraphContent from './graph.vue'

@Component
export default class ToolBox extends Vue {
  @Prop()
  isBrushing = false

  get graph() {
    return (this.$parent as GraphContent).graph
  }

  handleChange(e: MouseEvent) {
    const { graph } = this
    const id = (e.target as HTMLElement).id
    switch (id) {
      case 'undo':
        this.graph.undo()
        break
      case 'redo':
        this.graph.redo()
        break
      case 'expand':
        this.expand()
        break
      case 'shrink':
        this.shrink()
        break
      case 'reset':
        this.reset()
        break
      case 'select':
        this.brushSelect()
        break
      case 'fullScreen':
        this.graph.fullScreen()
        break
      case 'layout':
        graph.layout()
        break
      case 'fitView':
        this.graph.fitView()
        break
    }
  }

  expand() {
    this.graph.zoom(this.graph.getZoom() + 0.1)
  }

  shrink() {
    this.graph.zoom(this.graph.getZoom() - 0.1)
  }

  reset() {
    this.graph.zoom(1)
  }

  brushSelect() {
    this.graph.setBrushing(!this.graph.getBrushing())
    this.$emit('clickBrush', this.graph.getBrushing())
  }
}
